template>\r\n  <el-table\r\n    :data=\"anchors\"\r\n    stripe\r\n    max-height=\"400\"\r\n    style=\"width: 100%\"\r\n    sortable\r\n    :sort-by=\"['price', 'similarity']\"\r\n    :sort-order=\"['ascending', 'descending']\"\r\n  >\r\n    <el-table-column fixed prop=\"price\" label=\"价格 (元)\" width=\"100\" sortable>\r\n      <template #default=\"scope\">\r\n        <span v-html=\"formatPrice(scope.row.price)\"></span>\r\n      </template>\r\n    </el-table-column>\r\n\r\n    <el-table-column prop=\"similarity\" label=\"相似度\" width=\"80\" sortable  align=\"center\">\r\n      <template #default=\"scope\">\r\n        <el-tag :type=\"getSimilarityTagType(scope.row.similarity)\">\r\n          {{ scope.row.similarity.toFixed(3) }}\r\n        </el-tag>\r\n      </template>\r\n    </el-table-column>\r\n\r\n    <el-table-column prop=\"nickname\" label=\"角色\" width=\"60\" align=\"center\">\r\n      <template #default=\"scope\">\r\n       <RoleImage :roleInfo=\"scope.row.roleInfo\" :other_info=\"scope.row.other_info\" />\r\n      </template>\r\n    </el-table-column>\r\n    <el-table-column prop=\"server_name\" label=\"服务器\" width=\"90\">\r\n      <template #default=\"scope\">\r\n        <div class=\"server-info\">\r\n          <div>  <el-tag type=\"danger\" size=\"mini\">{{ getServerHeatLabel(scope.row.serverid)||'其他' }}</el-tag></div>\r\n          <div> <el-tag type=\"danger\" size=\"mini\">{{ scope.row.server_name }}</el-tag></div>\r\n          <div v-if=\"scope.row.is_cross_server\" class=\"cross-server-tag\">\r\n            <el-tag size=\"mini\" type=\"info\">跨服</el-tag>\r\n          </div>\r\n        </div>\r\n      </template>\r\n    </el-table-column>\r\n    <el-table-column prop=\"school_desc\" label=\"等级/门派\" width=\"80\" align=\"center\">\r\n      <template #default=\"scope\">\r\n        <el-tag size=\"mini\">\r\n          {{ scope.row.level }}\r\n        </el-tag>\r\n        <el-tag type=\"success\" size=\"mini\">\r\n          {{ scope.row.roleInfo.basic_info.school }}\r\n        </el-tag>\r\n      </template>\r\n    </el-table-column>\r\n    <el-table-column label=\"修炼信息\" min-width=\"70\">\r\n      <template #default=\"scope\">\r\n        <div class=\"cultivation-info\">\r\n          <div class=\"cultivation-row\">\r\n            <span class=\"cultivation-values\">\r\n              {{ scope.row.expt_ski1 || 0 }}/{{ scope.row.expt_ski2 || 0 }}/{{ scope.row.expt_ski3 || 0 }}/{{ scope.row.expt_ski4 || 0 }}\r\n            </span>\r\n          </div>\r\n          <div class=\"cultivation-row\">\r\n            <span class=\"cultivation-values\">\r\n              {{ scope.row.beast_ski1 || 0 }}/{{ scope.row.beast_ski2 || 0 }}/{{ scope.row.beast_ski3 || 0 }}/{{ scope.row.beast_ski4 || 0 }}\r\n            </span>\r\n          </div>\r\n          <div class=\"cultivation-row\" v-if=\"scope.row.total_cultivation\">\r\n            <span class=\"cultivation-label\">总修炼:</span>\r\n            <span class=\"cultivation-values total-cultivation\">{{ scope.row.total_cultivation }}</span>\r\n          </div>\r\n        </div>\r\n      </template>\r\n    </el-table-column>\r\n\r\n    <el-table-column label=\"特殊属性\" min-width=\"120\">\r\n      <template #default=\"scope\">\r\n        <div class=\"special-attributes\">\r\n          <div v-if=\"scope.row.sum_exp\" class=\"attr-item\">\r\n            <span class=\"attr-label\">总经验:</span>\r\n            <span class=\"attr-value\">{{ scope.row.sum_exp }}亿</span>\r\n          </div>\r\n          <div v-if=\"scope.row.all_new_point\" class=\"attr-item\">\r\n            <span class=\"attr-label\">乾元丹:</span>\r\n            <span class=\"attr-value special-value\">{{ scope.row.all_new_point }}点</span>\r\n          </div>\r\n          <div v-if=\"scope.row.three_fly_lv\" class=\"attr-item\">\r\n            <span class=\"attr-label\">化圣:</span>\r\n            <span class=\"attr-value special-value\">{{ scope.row.three_fly_lv }}重</span>\r\n          </div>\r\n          <div v-if=\"scope.row.school_history_count > 1\" class=\"attr-item\">\r\n            <span class=\"attr-label\">转门派:</span>\r\n            <span class=\"attr-value\">{{ scope.row.school_history_count }}次</span>\r\n          </div>\r\n        </div>\r\n      </template>\r\n    </el-table-column>\r\n    <el-table-column label=\"操作\" width=\"80\" fixed=\"right\">\r\n      <template #default=\"scope\">\r\n        <el-link :href=\"getCBGLinkByType(scope.row.eid)\" type=\"danger\" target=\"_blank\">藏宝阁</el-link>\r\n      </template>\r\n    </el-table-column>\r\n  </el-table>\r\n</template>\r\n\r\n<script>\r\nimport { commonMixin } from '@/utils/mixins/commonMixin'\r\nimport RoleImage from './RoleInfo/RoleImage.vue'\r\nexport default {\r\n  name: 'SimilarRoleTable',\r\n  components: {\r\n    RoleImage\r\n  },\r\n  mixins: [commonMixin],\r\n  props: {\r\n    anchors: {\r\n      type: Array,\r\n      required: true\r\n    }\r\n  },\r\n  methods: {\r\n    getServerHeatLabel(serverid){\r\n      const serverHeat = window.hotServersConfig?.find(item => item.children.find(child => child.server_id === serverid))\r\n      return serverHeat?.server_name||''\r\n    },\r\n    getSimilarityTagType(similarity) {\r\n      if (similarity >= 0.9) {\r\n        return 'success'\r\n      } else if (similarity >= 0.7) {\r\n        return 'primary'\r\n      } else if (similarity >= 0.5) {\r\n        return 'info'\r\n      } else if (similarity >= 0.3) {\r\n        return 'warning'\r\n      } else {\r\n        return 'danger'\r\n      }\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.price-cell {\r\n  font-weight: 600;\r\n  color: #e6a23c;\r\n}\r\n\r\n.role-name {\r\n  font-weight: 500;\r\n  color: #303133;\r\n}\r\n\r\n.cultivation-info {\r\n  font-size: 12px;\r\n}\r\n\r\n.cultivation-row {\r\n  display: flex;\r\n  align-items: center;\r\n  margin-bottom: 4px;\r\n}\r\n\r\n.cultivation-label {\r\n  color: #909399;\r\n  min-width: 60px;\r\n  font-size: 11px;\r\n}\r\n\r\n.cultivation-values {\r\n  color: #606266;\r\n  font-family: monospace;\r\n}\r\n\r\n.total-cultivation {\r\n  color: #409eff;\r\n  font-weight: 600;\r\n}\r\n\r\n.special-attributes {\r\n  font-size: 12px;\r\n}\r\n\r\n.attr-item {\r\n  display: flex;\r\n  align-items: center;\r\n  margin-bottom: 3px;\r\n}\r\n\r\n.attr-label {\r\n  color: #909399;\r\n  min-width: 50px;\r\n  font-size: 11px;\r\n}\r\n\r\n.attr-value {\r\n  color: #606266;\r\n}\r\n\r\n.special-value {\r\n  color: #e6a23c;\r\n  font-weight: 600;\r\n}\r\n\r\n.server-info {\r\n  text-align: center;\r\n}\r\n\r\n.cross-server-tag {\r\n  margin-top: 4px;\r\n}\r\n\r\n/* 表格行悬停效果 */\r\n.el-table tbody tr:hover > td {\r\n  background-color: #f5f7fa !important;\r\n}\r\n\r\n/* 相似度标签样式优化 */\r\n.el-tag {\r\n  font-weight: 500;\r\n}\r\n</style> "],"sourceRoot":""}]);
